import React from 'react';
import './App.css';
import { Link} from "react-router-dom";


class Details extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      user: '',
      contractInstance: '',
      user:'',
      networkId:"",
      runesRemainingToAssign:0,
      myRuneCounter:0,
      myRunes:[]
    };
  }

  componentWillMount(){
    this.setState({
      hasData:true
    })
  }
  componentWillUnmount(){
    window.scrollTo(0, 0);
  }


  render() {

    if (this.state.hasData) {
      return (
        <div className="App"   >
          <section >
            <div >
              <div>
              <h1>
                About Crypto Runes
              </h1>
           
              <br />
              Claim your Rune NFT on Avalanche. <br/>
              Contract updated! <br/>New runes have been airdropped to people that interacted with the old contract.
              <br/><br/><br/>
              <h2>There are 6 tiers of runes:</h2>
              <br/><br/>
              {/** <br /><br/><br/>
              <h2>Rune prices:</h2><br/><br/>*/}
              Tier 1 - the first one hundred NFTs: are free (only pays gas fees)<br/><br/>
              Tier 2 - the following one hundred NFTs: 0.75 $AVAX<br/><br/>
              Tier 3 - the following one hundred NFTs: 1.5 $AVAX<br/><br/>
              Tier 4 - the following one hundred NFTs: 3 $AVAX<br/><br/>
              Tier 5 - the following one hundred NFTs: 6 $AVAX<br/><br/>
             
              <br/>
              Go back to the <Link to="/">homepage</Link>
              </div>
              </div>
          </section>
        </div>
      );
    } else {
      return (
        <div className="App" >
          <section >
          <div className="sk-folding-cube">
  <div className="sk-cube1 sk-cube"></div>
  <div className="sk-cube2 sk-cube"></div>
  <div className="sk-cube4 sk-cube"></div>
  <div className="sk-cube3 sk-cube"></div>
</div>
          </section>
        </div>
      );
    }

  }
}

export default Details;
